.Details{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / 34%);
    top: 0;
    left: 0;
    z-index: 5;
}
.Details_Container span {
    position: absolute;
    right: -7px;
    top: -7px;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    background: rgb(172, 52, 52);
    border-radius: 999px;
    cursor: pointer;
}
.Details_Container span i{
    font-size: 0.7em;

}
.Details_Container {
    position: absolute;
    display: flex;
    width: 90%;
    height: 90%;
    background-color: aliceblue;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
}

.Details_Container .Details_Image{
    width: 65%;
    height: 100%;
}

.Details_Container .Details_Image img{
    width: 100%;
    height: 100%;
    border-radius: 10px;
    object-fit: cover;
}
.Details_Content{
    display: grid;
    align-content: space-evenly;    
    width: 35%;
    margin: 10px 20px;
    overflow-x: hidden;
    box-sizing: border-box;
    max-height: 90%;
}
.Details_Content .skills{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
/* TODO make this button as component */
.Details_Content a {
    box-sizing: border-box;
    width: 100%;
    padding: 4px;
    font-size: 0.8em;
    font-weight: 400;
    color: #fff;
    background-color: #5550cf;
    border-radius: 10px;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

@media screen and (max-width: 900px) {
    .Details_Container {
        display: grid;
    }
    .Details_Container .Details_Image{
        width: 100%;
        height: 80%;
    }
    .Details_Content{
        width: 90%;
        margin: auto;
    }
}